Wee.fn.make('todo', {
	init: function() {
		Wee.view.addHelper({
			sort: function(key, type, order) {
				if (this.val.length) {
					return this.val.sort(function(a, b) {
						return a[key] < b[key] ? -1 : 1;
					});
				} else {
					return false;
				}
			},
			otherTest: function() {
				console.log();
			}
		});

		var app = Wee.app.make('todo', {
			view: 'welcome.todo',
			target: 'ref:todo',
			model: [
				{
					label: 'Download and run Wee',
					done: true
				},
				{
					label: 'Explore the guide and welcome modules'
				},
				{
					label: 'Reset your project to a blank slate'
				},
				{
					label: 'Customize wee.json'
				},
				{
					label: 'Build something incredible'
				}
			]
		});

		$('ref:toggle').on('click', function() {
			var id = $(this).data('id'),
				val = app.$get(id + '.done') ? false : true;

			app.$set(id + '.done', val);
		});
	}
});